<template>
  <modal name = "lecture-feedback">
    <div align = "center">
      <p>Rating</p>
      <div>
        <star-rating :star-size = 20></star-rating>
      </div>
      <p>Feedback</p>
      <textarea></textarea>
      <br/>
      <button class = "btn btn-primary" type = "button" @click = "close">Submit</button>
    </div>
  </modal>
</template>

<script>
  import StarRating from 'vue-star-rating'
  export default {
    name: 'lecture-feedback',
    components:{
      StarRating
    },
    data(){
      return {
        lectureName: "",
        description: ""
      }
    },
    methods: {
      close: function() {
        this.$modal.hide("lecture-feedback");
      }
    },
  };
</script>

<style>
  .modal-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .modal {
    background: #FFFFFF;
    box-shadow: 2px 2px 20px 1px;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
  }

  .modal-header,
  .modal-footer {
    padding: 15px;
    display: flex;
  }

  .modal-header {
    border-bottom: 1px solid #eeeeee;
    color: #4AAE9B;
    justify-content: space-between;
  }

  .modal-footer {
    border-top: 1px solid #eeeeee;
    justify-content: flex-end;
  }

  .modal-body {
    position: relative;
    padding: 20px 10px;
  }

  .btn-close {
    border: none;
    font-size: 20px;
    padding: 20px;
    cursor: pointer;
    font-weight: bold;
    color: #4AAE9B;
    background: transparent;
  }

  .btn-green {
    color: white;
    background: #4AAE9B;
    border: 1px solid #4AAE9B;
    border-radius: 2px;
  }
</style>



